.tab {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  display: flex;
  align-items: center;
  line-height: 68rpx;
  .active {
    color: #ff5975;
  }
}

.mt70 {
  margin-top: 50rpx;
}

.title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.goods-info {
  display: flex;
  align-items: center;
}
.goods-img {
  width: 140rpx;
  height: 140rpx;
}